// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
//App
import '@/assets/css/reset_xsf0409.css';//reset css
import '@/assets/css/public.css';//css
import '@/assets/css/layer.css';//layer
//import 'swiper/dist/css/swiper.css';//css
import App from './App'
import router from './router'
import axios from 'axios'
import qs from 'qs'
import VueAxios from 'vue-axios'
//import VueAwesomeSwiper from 'vue-awesome-swiper';
//Vue.use(VueAwesomeSwiper);
import common from '@/assets/js/public.js';
Vue.prototype.common = common;

axios.defaults.timeout = 20000;//
axios.defaults.baseURL = common.ServerHost;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
//axios.defaults.headers.post['X-Requested-With'] = 'XMLHttpRequest';

axios.interceptors.request.use((config) => {
	if(config.method === 'post') {
		config.data = qs.stringify(config.data);
	}else if(config.method === 'get'){
		config.params = config.data;
		delete config.data;//data
	}else{};
	return config;
}, (error) => {
	return Promise.reject(error);
});

//
axios.interceptors.response.use((res) => {
//
//	if(res.data.status !== 0) {
//		return Promise.reject(res);//catch
//	};
	return res;//then
}, (error) => {
	//404
	return Promise.reject(error);
});

//vue-axiosaxios
//use
Vue.use(VueAxios, axios);
//commonVue
Vue.config.productionTip = true;

import headerPulic from '@/components/publicEg/headPublic';
import errorImg from '@/components/publicEg/errorImg';
import loadingGif from '@/components/publicEg/loadingGif'
Vue.component('header-pulic', headerPulic);//
Vue.component('img-error', errorImg);//
Vue.component('loading-gif', loadingGif);//

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



