//js
let ServerHost = 'https://yftest.fujuhaofang.com';
//let ServerHost = 'https://hifang.fujuhaofang.com';
if(~location.origin.indexOf('https://hifang.fujuhaofang.com') || ~location.origin.indexOf('https://hifang-pre.fujuhaofang.com')) {
	ServerHost = location.origin;
};
import axios from 'axios';
import '@/assets/js/layer041002.js';
export default {
	ServerHost,
	defaultSign: '',
	isAndroid() {
		//
		let u = navigator.userAgent;
		return(~u.indexOf('Android') || ~u.indexOf('Linux')) ? true : false;
	},
	isIos() {
		//ios
		return ~navigator.userAgent.indexOf('iPhone') ? true : false;
	},
	isWeixinBrowser() {
		//
		return ~navigator.userAgent.indexOf('MicroMessenger') ? true : false;
	},
	isMobile(text) {
		//
		return /^1\d{10}$/.test(text) ? true : false;
	},
	backNowDate(s) {
		//2018-05-11, a.toLocaleDateString();2018/5/11
		let g = arguments.length === 0 ? '-' : s,
			a = new Date(),
			f = v => (v < 10 ? ('0' + v) : ('' + v));
		return a.getFullYear() + g + f(a.getMonth() + 1) + g + f(a.getDate());
	},
	timestampToTime(timestamp) {
		//2018-09-10 17:09:06
        let date = new Date(timestamp * 1000),//10*1000131000
        	f = v => (v < 10 ? ('0' + v) : ('' + v)),
        	Y = date.getFullYear() + '-',
	        M = f(date.getMonth()+1) + '-',
	        D = f(date.getDate()) + ' ',
	        h = f(date.getHours()) + ':',
	        m = f(date.getMinutes()) + ':',
	        s = f(date.getSeconds());
        return Y+M+D+h+m+s;
   },
	urlDeal(urlStr, httpStr) {
		//
		return urlStr.replace(/(http|https):\/\//g, (httpStr ? (httpStr + ':') : location.protocol) + '//');
	},
	hideTel(str) {
		//*
		if(str) {
			let _arr = String(str).split('');
			_arr.splice(3, 4, '****');
			return _arr.join('');
		} else {
			return str;
		}
	},
	transCn(num) {
	lete=String(num).split(''),
			a = [];
		e.forEach((v,i)=>{
			a[i]=['','','','','','','','','',''][e[i]];
		});
	returna.join('');
	},
	jineComma(s, n) {
		//32, 348, 000
		if(s) {
			if(n === 0) {
				let a = String(parseInt(s)),
					l = a.split('.')[0].split('').reverse(),
					t = '';
				for(let i = 0; i < l.length; i++) {
					t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length ? ',' : '');
				};
				return t.split('').reverse().join('').replace(/\,/g, ', ');
			} else {
				let n = n > 0 && n <= 20 ? n : 2,
					a = parseFloat((s + '').replace(/[^\d\.-]/g, '')).toFixed(n) + '',
					l = a.split('.')[0].split('').reverse(),
					r = a.split('.')[1],
					t = '';
				for(let i = 0; i < l.length; i++) {
					t += l[i] + ((i + 1) % 3 == 0 && (i + 1) != l.length ? ',' : '');
				};
				return(t.split('').reverse().join('') + '.' + r).replace(/\,/g, ', ');
			};
		} else {
			return s;
		}
	},
	dealJineNum(num) {
		//
		if(isFinite(num)) {
			if(parseInt(num) == num || num.toString().split('.')[1].length < 3) {
				return num;
			} else {
				return Number((num).toFixed(2));
			}
		} else {
			return 0;
		};
	},
	duringRequest(){
		//
		let arr = [...arguments];//argumentsarguments
		axios.interceptors.request.use((config) => {
			arr.forEach((a, i) => {
				if(~config.url.indexOf(a.urlStr)){
					a.startAction();//
				};
			});
			
			return config;
		}, (error) => {
			return Promise.reject(error);
		});
		
		//
		axios.interceptors.response.use((res) => {
			arr.forEach((a, i) => {
				if(~res.config.url.indexOf(a.urlStr)){
					a.endAction();//
				};
			});
			return res;
		}, (error) => {
			return Promise.reject(error);
		});
	},
	h5Page(token, fn) {
		//token
		let _this = this;
		axios({
			method: 'get',
			url: _this.ServerHost + '/api/user/userinfo',
			responseType: 'json',
			data: {
				'authToken': token
			}
		})
		.then(function(response) {
			if(response.data.code == 200) {
				//webviewlocalStoragenull,webview
				localStorage.setItem('token', response.data.data.token); //token
				localStorage.setItem('userid', response.data.data.id); //id
				localStorage.setItem('userlevel', response.data.data.level); //id
				localStorage.setItem('userphone', response.data.data.phone); //
				localStorage.setItem('username', response.data.data.name); //
				localStorage.setItem('userinfo', encodeURIComponent(JSON.stringify(response.data.data))); //
				fn && fn();
			} else {
				layer.tipsX(response.data.msg);
				return false;
			}
		})
		.catch(function(error) {
			layer.tipsX(error);
		});
	},
	Compatible(obj) {
		//app
		if(this.isIos()) {
			try {
				if(obj['hrefIos']) {
					window.location.href = obj['hrefIos'];
				} else {
					window.webkit.messageHandlers.AppModel.postMessage(obj.paraIos); //webkit.messageHandlers.AppModel.postMessageios
				}
			} catch(e) {
				obj.fn();
			};
		} else if(this.isAndroid()) {
			try {
				obj.fnAndroid(); //android.backPressed(),webview
			} catch(e) {
				//alert(e.name + '\n' + e.message);
				obj.fn();
			}
		} else {
			console.log("other device");
			obj.fn();
		};
	},
	//
	userBehavior(obj,fn) {
		axios({
			method: 'post',
			url: '/api/behavior/insertBehavior',
			responseType: 'json',
			data: obj
		})
		.then(function(response) {
			if(response.data.code === 200) {
				
			} else {
				layer.tipsX(response.data.message);
			};
			fn && fn();
		})
		.catch(function(error) {
			layer.tipsX(error);
		});
	}
};


